<script lang="ts">import { onMount } from "svelte";
import Device from "./components/device.svelte";
import SelectDevoce from "./components/select-device.svelte";
let serial;
let modbusCmd;
let selectDevice = ["0001", "0002", "0003", "0004", "0005"];
let selectDeviceShow = false;
export let name;
electronInit();
onMount(() => {
    console.log("进入组件", navigator);
    // ipcf();
});
function electronInit() {
    if (window["sysElectron"]) {
        serial = window["serial"];
        modbusCmd = window["modbusCmd"];
    }
    else {
        console.log(navigator.userAgent);
    }
}
function y01() {
    // ipc.send("no");
    serial.writeComm("0001:01050500FF00F6\r\n", (data) => {
        console.log(data.data, data.success);
    });
}
function y00() {
    // ipc.send("off");
    serial.writeComm("0001:010505000000F5\r\n");
}
function d0() {
    // ipc.send("off");
    console.log(selectDevice);
    const cmd = modbusCmd(1, 3, 4096, 20, "int32", "ascii");
    console.log(cmd);
    selectDevice.map((m) => {
        const cmd1 = `${m}${cmd}`;
        console.log(cmd1);
        serial.writeComm(cmd1, (data) => {
            console.log(m, data.data, data.success);
        });
    });
}
function getStyle() {
    const root = document.querySelector(":root");
    var sroot = getComputedStyle(root);
    console.log(sroot.getPropertyValue("--my-color"));
    root.style.setProperty("--my-color", "red");
    root.style.setProperty("--body-bgc", "#FFF");
}
function colorInput(event) {
    console.log(event.target.value);
    const root = document.querySelector(":root");
    root.style.setProperty("--my-color", event.target.value);
}
function deviceClick() {
    selectDeviceShow = true;
}
function hiddenSelectDevice() {
    selectDeviceShow = false;
}
</script>

<style lang="scss">:root {
  --my-color: pink;
  --body-bgc: #fff;
  --bgc-color: #242549;
  --bgc-color-1: #383b55;
