  --bgc-active-1: #ca1c59;
  --mm-color: #56c05d;
  --mpa-color: #ca1c59;
  --font-size-1: 32px;
  --font-size-2: 24px;
  --font-size-3: 16px;
  --font-color-default: #aaa;
  --font-color-active: #fff; }

:global(body, main, div, button, p, input) {
  margin: 0;
  padding: 0; }

:global(body) {
  background-color: var(--body-bgc); }

:global(.input-content) {
  display: flex;
  align-items: center; }
