    flex-direction: column; }
  :global(.input-content) input {
    padding: 3px 5px; }
  :global(.input-content).mpa input {
    border: 1px solid var(--mpa-color); }
  :global(.input-content).mm input {
    border: 1px solid var(--mm-color); }

:global(.input-content.mpa input) {
  border: 1px solid var(--mpa-color); }

:global(.modal) {
  z-index: 1001;
  position: fixed;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100vw;
  background-color: rgba(0, 0, 0, 0.3);
  display: flex;
  justify-content: center;
  align-items: center; }

:global(.modal::before) {
  z-index: -1;
