  width: 100vw;
  background-color: rgba(0, 0, 0, 0.5); }

main {
  display: flex;
  flex-direction: column;
  height: 100%; }

.device {
  display: flex;
  flex: 1;
  height: 100%;
  width: 100%;
  overflow-x: auto; }

.operation {
  height: 48px;
  border-top: 1px solid #ccc;
  display: flex;
  align-items: center; }
</style>

