<main>
  <!-- <h1>svelte-electron-template3.0</h1> -->

  <div class="device">
    {#each selectDevice as dev, i}
      <Device name={dev} />
    {/each}
  </div>
  <div class="operation">
    <button on:click={deviceClick}>设备选择</button>
    <div class="input-conent">
      设置位移：
      <input type="number" />
    </div>
    <div class="input-conent">
      平衡控制：
      <input type="number" />
    </div>
    <div class="input-conent">
      持压压力：
      <input type="number" />
    </div>
    <button>启动</button>
    {#if selectDeviceShow}
      <div class="modal" on:click|self={hiddenSelectDevice}>
        <SelectDevoce bind:selectDevice />
      </div>
    {/if}
  </div>
  <!-- <h1>Hello {name}!</h1> -->
</main>
