<script lang="ts">import { onDestroy, onMount } from "svelte";
export let name = "？号机";
let destroy = false;
const serial = window["serial"];
const modbusCmd = window["modbusCmd"];
let deviceData;
let linkState = false;
const jacks = ["A", "B"];
let selectJacks = ["A", "B"];
function getLiveData() {
    new Promise((resolve, reject) => {
        const cmd = modbusCmd(1, 3, 4096, 20, "int32", "ascii");
        serial.writeComm(`${name}${cmd}`, (data) => {
            console.log(name, data.data, data.success);
            if (data.success) {
                // resolve(data.data.float.map((f) => f.toFixed(2)));
                deviceData = data.data.float.map((f) => f.toFixed(2));
                if (!destroy) {
                    // getLiveData();
                }
            }
            linkState = data.success;
        });
    });
}
onMount(() => {
    // getLiveData();
});
onDestroy(() => {
    destroy = true;
});
</script>

